:root {
  --blue: #007bff;
  --indigo: #6610f2;
  --purple: #52057b;
  --pink: #e14594;
  --red: #dc3545;
  --orange: #d65a31;
  --yellow: #ffc107;
  --green: #28a745;
  --teal: #4ecca3;
  --cyan: #17a2b8;
  --white: #fff;
  --gray: #3c4f65;
  --gray-dark: #343a40;
  --primary: #bc6ff1;
  --secondary: #892cdc;
  --success: #a7d129;
  --info: #acdbdf;
  --warning: #ffd700;
  --danger: #dc3545;
  --light: #eeeeee;
  --dark: #000000;
  --breakpoint-xs: 0;
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
  --font-family-sans-serif: -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas,
    "Liberation Mono", "Courier New", monospace;
}

/* elements that change color */
h1,
p {
  color: var(--text);
}

h2 {
  color: var(--heading);
}

#container {
  min-height: calc(100vh - 70px);
}
#footer {
  height: 100px;
  margin-top: -50px;
}
.btn-circle {
  border-radius: 50%;
}
hr {
  width: 100%;
  text-align: left;
  margin-left: 0;
}
